body {
    font-family: Helvetica;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 24px;
    color: $mainText;
}
@mixin h1() {
    font-style: normal;
    font-weight: bold;
    font-size: 36px;
    line-height: 40px;
}
@mixin h2() {
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 32px;
}
@mixin h3() {
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 24px;
}
@mixin subtitle-1() {
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 24px;
}
@mixin subtitle-1-alt() {
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 24px;
}
@mixin subtitle-2() {
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 24px;
}
@mixin subtitle-2-alt() {
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 18px;
}
@mixin caption() {
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 16px;
}
@mixin small-text() {
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 16px;
}